import React, { Component } from 'react';
import './index.css';
import { GuitarChord, ChordName, ChordSvg } from '../../guitarChord.js';

class ChordDraw extends Component {
    constructor(props) {
        super(props);
        this.state = {
            info: "",
            currentChordTone: null,
            currentChordName: "",
            currentChordResults: []
        }
        this.guitarChord = new GuitarChord();
        this.info = {
            hasAnswer: [
                "",
                "bingo",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                ""
            ],
            hasSvg: [
                "",
                "",
                "",
                "",
                "",
                "( - )",
                "",
                "",
                "()",
                "",
                "",
                ""
            ],
            noAnswer: [
                "",
                "",
                "kidding?",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "......",
                " ()",
                " (-_-)",
                " _(:)_",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                ""
            ],
        }
    }

    handleChordClick = () => {
        if (this.state.currentChordTone) {
            this.guitarChord.playChord(this.state.currentChordTone);
        }
    }

    randomOne(arr) {
        return arr[Math.floor(Math.random() * arr.length)];
    }

    saveChord = (index) => {
        const svgElement = document.querySelectorAll("#chord_draw svg")[index];
        if (!svgElement) return;

        // SVG
        const newSvg = svgElement.cloneNode(true);
        newSvg.setAttribute("width", "120");
        newSvg.setAttribute("height", "160");
        
        // 
        const elements = newSvg.querySelectorAll('*');
        elements.forEach(el => {
            if (el.getAttribute('class')) {
                el.setAttribute('class', el.getAttribute('class')); // 
            }
        });

        // 
        const chordNameTexts = newSvg.querySelectorAll('.chord-name text');
        chordNameTexts.forEach(text => {
            text.setAttribute("x", "60");
            text.setAttribute("y", "20");
            text.setAttribute("text-anchor", "middle");
            text.setAttribute("font-size", "14");
            text.textContent = this.state.currentChordName;
        });

        // 
        const rects = newSvg.querySelectorAll('rect');
        rects.forEach(rect => {
            if (!rect.getAttribute('class')) {
                rect.setAttribute('class', 'chord-rect');
            }
        });

        const lines = newSvg.querySelectorAll('line');
        lines.forEach(line => {
            if (!line.getAttribute('class')) {
                line.setAttribute('class', 'chord-gird');
            }
        });

        const circles = newSvg.querySelectorAll('circle');
        circles.forEach(circle => {
            if (!circle.getAttribute('class')) {
                circle.setAttribute('class', 'chord-block-circle');
            }
        });

        // 
        if (this.props.onSaveChord) {
            this.props.onSaveChord({
                id: Date.now(),
                name: this.state.currentChordName,
                tones: [...this.state.currentChordTone],
                svgContent: newSvg.outerHTML
            });
        }
    }

    draw(chordTone) {
        this.setState({ 
            currentChordTone: chordTone,
            currentChordResults: []
        });
        
        let chordName = new ChordName().getChordName(chordTone);
        let chordResult = this.guitarChord.chord(chordTone);
        let svg = new ChordSvg();
        let container = document.getElementById("chord_draw");
        container.innerHTML = "";

        setTimeout(() => {
            this.props.hideLoading();
        }, 200);
        
        setTimeout(() => {
            if (chordResult.length === 0) {
                this.setState({
                    info: this.randomOne(this.info.noAnswer),
                    currentChordName: "",
                    currentChordResults: []
                });
            } else if (/\.\.\./.test(chordName)) {
                this.setState({
                    info: this.randomOne(this.info.hasSvg),
                    currentChordName: "Unknown Chord",
                    currentChordResults: chordResult
                });
            } else {
                this.setState({
                    info: this.randomOne(this.info.hasAnswer),
                    currentChordName: chordName,
                    currentChordResults: chordResult
                });
            }
            
            // 
            chordResult.forEach((chordItem, index) => {
                const wrapper = document.createElement('div');
                wrapper.className = 'chord-wrapper';
                
                // 
                const svgContainer = document.createElement('div');
                svgContainer.className = 'chord-svg-container';
                
                // 
                svg.drawChord(chordTone, chordItem, svgContainer);
                
                // 
                const saveButton = document.createElement('button');
                saveButton.className = 'save-chord-btn';
                saveButton.textContent = '';
                saveButton.onclick = (e) => {
                    e.stopPropagation();
                    this.saveChord(index);
                };
                
                wrapper.appendChild(svgContainer);
                wrapper.appendChild(saveButton);
                container.appendChild(wrapper);
            });
        }, 500);
    }

    render() {
        return (
            <div className="container-chordDraw">
                <div className={"draw-info" + (this.state.info ? " show" : "")}>{this.state.info}</div>
                <div id="chord_draw" onClick={this.handleChordClick}></div>
            </div>
        );
    }
}

export default ChordDraw;


